"use client";

import React, { useState } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";

// Mock data for demonstration
const products = [
  {
    id: 1,
    name: "Traditional Silk Saree",
    image: "https://images.pexels.com/photos/2995309/pexels-photo-2995309.jpeg",
    description: "Handwoven silk saree with intricate designs",
    moq: 10,
    prices: {
      tier1: "₹2,999",
      tier2: "₹2,799",
      tier3: "₹2,599",
    },
  },
  {
    id: 2,
    name: "Designer Cotton Saree",
    image: "https://images.pexels.com/photos/2983464/pexels-photo-2983464.jpeg",
    description: "Premium cotton saree with modern prints",
    moq: 15,
    prices: {
      tier1: "₹1,999",
      tier2: "₹1,799",
      tier3: "₹1,599",
    },
  },
  // Add more products as needed
];

interface RegionPageProps {
  params: {
    region: string;
  };
}

export default function RegionPage({ params }: RegionPageProps) {
  const [selectedProducts, setSelectedProducts] = useState<number[]>([]);

  const toggleProductSelection = (productId: number) => {
    setSelectedProducts((prev) =>
      prev.includes(productId)
        ? prev.filter((id) => id !== productId)
        : [...prev, productId]
    );
  };

  return (
    <div className="max-w-7xl mx-auto py-8">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-3xl font-bold text-purple-700 capitalize">
          {params.region} Region Catalog
        </h1>
        {selectedProducts.length > 0 && (
          <Button
            onClick={() => console.log("Inquiry cart:", selectedProducts)}
            className="bg-purple-600 hover:bg-purple-700"
          >
            View Inquiry Cart ({selectedProducts.length})
          </Button>
        )}
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {products.map((product) => (
          <Card
            key={product.id}
            className={`overflow-hidden transition-shadow hover:shadow-lg ${
              selectedProducts.includes(product.id)
                ? "border-2 border-purple-500"
                : ""
            }`}
          >
            <img
              src={product.image}
              alt={product.name}
              className="w-full h-64 object-cover"
            />
            <CardContent className="p-4">
              <div className="flex justify-between items-start mb-2">
                <h3 className="text-lg font-semibold">{product.name}</h3>
                <Badge variant="secondary">MOQ: {product.moq}</Badge>
              </div>
              <p className="text-gray-600 text-sm mb-4">{product.description}</p>

              <div className="space-y-2 mb-4">
                <div className="flex justify-between text-sm">
                  <span className="text-gray-600">Tier 1 (1-50 pcs)</span>
                  <span className="font-semibold">{product.prices.tier1}</span>
                </div>
                <div className="flex justify-between text-sm">
                  <span className="text-gray-600">Tier 2 (51-100 pcs)</span>
                  <span className="font-semibold">{product.prices.tier2}</span>
                </div>
                <div className="flex justify-between text-sm">
                  <span className="text-gray-600">Tier 3 (100+ pcs)</span>
                  <span className="font-semibold">{product.prices.tier3}</span>
                </div>
              </div>

              <Button
                onClick={() => toggleProductSelection(product.id)}
                className={`w-full ${
                  selectedProducts.includes(product.id)
                    ? "bg-purple-100 text-purple-700 hover:bg-purple-200"
                    : "bg-purple-600 text-white hover:bg-purple-700"
                }`}
              >
                {selectedProducts.includes(product.id)
                  ? "Remove from Inquiry"
                  : "Add to Inquiry"}
              </Button>
            </CardContent>
          </Card>
        ))}
      </div>

      <div className="fixed bottom-4 right-4">
        <a
          href="https://wa.me/1234567890"
          target="_blank"
          rel="noopener noreferrer"
          className="bg-green-500 hover:bg-green-600 text-white rounded-full p-4 shadow-lg flex items-center space-x-2"
        >
          <svg
            className="w-6 h-6"
            fill="currentColor"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path d="M12.031 6.172c-3.181 0-5.767 2.586-5.768 5.766-.001 1.298.38 2.27 1.019 3.287l-.582 2.128 2.182-.573c.978.58 1.911.928 3.145.929 3.178 0 5.767-2.587 5.768-5.766.001-3.187-2.575-5.77-5.764-5.771zm3.392 8.244c-.144.405-.837.774-1.17.824-.299.045-.677.063-1.092-.069-.252-.08-.575-.187-.988-.365-1.739-.751-2.874-2.502-2.961-2.617-.087-.116-.708-.94-.708-1.793s.448-1.273.607-1.446c.159-.173.346-.217.462-.217l.332.006c.106.005.249-.04.39.298.144.347.491 1.2.534 1.287.043.087.072.188.014.304-.058.116-.087.188-.173.289l-.26.304c-.087.086-.177.18-.076.354.101.174.449.741.964 1.201.662.591 1.221.774 1.394.86s.274.072.376-.043c.101-.116.433-.506.549-.68.116-.173.231-.145.39-.087s1.011.477 1.184.564.289.13.332.202c.045.072.045.419-.1.824zm-3.423-14.416c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm.029 18.88c-1.161 0-2.305-.292-3.318-.844l-3.677.964.984-3.595c-.607-1.052-.927-2.246-.926-3.468.001-3.825 3.113-6.937 6.937-6.937 1.856.001 3.598.723 4.907 2.034 1.31 1.311 2.031 3.054 2.03 4.908-.001 3.825-3.113 6.938-6.937 6.938z"/>
          </svg>
          <span>Chat with us</span>
        </a>
      </div>
    </div>
  );
}
